import React from 'react'
import styled from 'styled-components';
import logo from '../img/logo.png';
import PrimaryButton from './PrimaryButton';

function ImageSection() {
    return (
        <ImageSectionStyled>
            <div className="left-content">
                <img src={logo} alt=""/>
            </div>
            <div className="right-content">
                <h4>BEST<span>NFT Site</span></h4>
                <p className="paragraph">
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Laudantium, commodi ratione incidunt harum minus dolorem sapiente et obcaecati? Neque culpa, ex itaque id ut molestias, possimus laudantium minus obcaecati quod molestiae recusandae? Nostrum alias asperiores dolorem? Nam, ad veritatis. Consectetur recusandae accusantium consequatur omnis velit dolor quisquam vel non fuga natus, necessitatibus, distinctio unde maxime dolorum repudiandae illo iste rerum!
 
                    
                </p>
                
                {/* <PrimaryButton title={'Download Cv'} /> */}
            </div>
        </ImageSectionStyled>
    )
}


const ImageSectionStyled = styled.div`
    margin-top: 5rem;
    display: flex;
    @media screen and (max-width:1000px){
        flex-direction: column;
        .left-content{
            margin-bottom: 2rem;
        }
    }
    .left-content{
        width: 100%;
        img{
            width: 95%;
            object-fit: cover;
        }
    }
    .right-content{
        width: 100%;
        h4{
            font-size: 2rem;
            color: var(--white-color);
            span{
                font-size: 2rem;
            }
        }
        .paragraph{
            padding: 1rem 0;
        }
        .about-info{
            display: flex;
            padding-bottom: 1.4rem;
            .info-title{
                padding-right: 3rem;
                p{
                    font-weight: 600;
                }
            }
            .info-title, .info{
                p{
                    padding: .3rem 0;
                }
            }
        }
    }
`;
export default ImageSection;
